<template>
  <div>
    <v-row class="d-flex" style="max-width:60vw; margin:auto;">
      <v-col cols="2">
      </v-col>
      <v-col class="d-flex align-center" cols="2">
        <v-select
          label=" "
          :items="jobFilter"
          v-model="searchFilter"
          @input="searchJob"
          :disabled="filteringDisabled"
        >
        </v-select>
      </v-col>
      <v-col class="d-flex" cols="5">
        <v-text-field
          style="max-width: 800px;"
          v-model="searchWord"
          append-icon="mdi-magnify"
          :label="searchBarLabel"
          @keypress.enter="searchCompany"
          :disabled="searchBarDisabled"
        ></v-text-field>
      </v-col>
      <v-col cols="1">
      </v-col>
      <v-col class="d-flex" cols="2">
      <v-btn
          text
          large
          class="m-3 primary-color text-white rounded-0"
          v-if="isCompany"
          @click="$router.push({ name: 'wantedregist' })">
            </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data: function () {
    return {
      //  
      searchWord: '',
      ...mapState('wanted', {
        companies: state => state.companies
      }),
      //  
      searchFilter: '',
      jobFilter: [
        '',
        'IT',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '/',
        '',
      ],
    }
  },
  computed: {
    //  disable  (  true)
    searchBarDisabled: function () {
      if (this.searchFilter != "") {
        return true
      } else {
        return false
      }
    },
    filteringDisabled: function () {
      if (this.searchWord) {
        return true
      } else {
        return false
      }
    },
    searchBarLabel: function () {
      if (this.searchFilter != "") {
        return '   '
      } else {
        return ' '
      }
    },
    isCompany: function () {
      if (localStorage.getItem('login_type') === 'company') {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    searchAll: function () {
      this.searchWord = ''
      this.searchFilter = ''
      this.$store.dispatch('wanted/setCompanies')
    },
    searchCompany: function () {
      if (!this.searchWord) {
        Swal.fire({ 
          icon: 'warning', // Alert  
          title: ' .', // Alert  
          text: '   .', // Alert  
        });
      } else {
        this.$store.dispatch('wanted/searchCompany', this.searchWord)
      }
    },
    searchJob: function () {
      if (this.searchFilter === '')
        this.searchAll();
      else
        this.$store.dispatch('wanted/searchJob', this.searchFilter)
    }
  }

}
</script>

