<template>
  <div class="pt-1">
    <v-form v-model="valid">
      <h1 align="center" class="my-4"> </h1>
      <v-container style="max-width: 800px; background: #EAF5F1;">
        <v-row class="justify-center">
          <br />
          <v-col cols="12">
            <v-row>
              <v-col cols="6">
                <v-select
                  label=" "
                  :items="jobs"
                  :rules="jobRules"
                  v-model="registRecruitForm.job"
                >
                </v-select>
              </v-col>
              <v-col cols="6">
                <v-text-field label=" " v-model="registRecruitForm.jobDetail">
                </v-text-field>
              </v-col>
            </v-row>
            <v-row class="d-flex align-center">
              <v-col cols="6">
                <v-radio-group
                  row
                  v-model="registRecruitForm.career"
                  :rules="careerRules"
                  label="/"
                >
                  <v-radio label="" value=""></v-radio>
                  <v-radio label="" value=""></v-radio>
                </v-radio-group>
              </v-col>
              <v-col cols="6">
                <v-menu
                  v-model="dateCalendar"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="recruitDate.join(' ~ ')"
                      label=" "
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="recruitDate"
                    range
                    @input="dateCalendarClose"
                  ></v-date-picker>
                </v-menu>
              </v-col>
            </v-row>
            <v-row>
              <v-col cols="6">
                <v-menu
                  v-model="descriptionCalendar"
                  :close-on-content-click="false"
                  :nudge-right="40"
                  transition="scale-transition"
                  offset-y
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="pptDate"
                      label=" "
                      prepend-icon="mdi-calendar"
                      readonly
                      v-bind="attrs"
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="pptDate"
                    @input="descriptionCalendar = false"
                  ></v-date-picker>
                </v-menu>
              </v-col>
              <v-col cols="6">
                <v-text-field type="time" v-model="pptTime" label="  ">
                </v-text-field>
              </v-col>
            </v-row>
            <v-text-field
              label=" url"
              :rules="descriptionURLRules"
              v-model="registRecruitForm.descriptionURL"
            >
            </v-text-field>
            <v-text-field
              label=" url"
              :rules="recruitURLRules"
              v-model="registRecruitForm.recruitURL"
            >
            </v-text-field>
          </v-col>
        </v-row>
      </v-container>
      <div style="height: 200px;" class="py-4">
        <v-row class="d-flex justify-center">
          <v-col cols="2">
            <v-btn
              block
              text
              x-large
              class="primary-color text-white rounded-0"
              @click="registRecruit"
            >
              
            </v-btn>
          </v-col>
          <v-col cols="2">
            <v-btn
              block
              text
              x-large
              class="secondary-color text-white rounded-0"
              @click="cancel()"
            >
              
            </v-btn>
          </v-col>
        </v-row>
      </div>
    </v-form>
  </div>
</template>

<script>
import router from '@/router';
import Http from '@/http.js';
export default {
  name: 'WantedRegist',
  data: function() {
    return {
      valid: false,
      jobRules: [(v) => !!v || '  '],
      careerRules: [(v) => !!v || '  '],
      descriptionURLRules: [(v) => !!v || '  '],
      recruitURLRules: [(v) => !!v || '  '],
      jobs: [
        'IT',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '/',
        '',
      ],
      recruitDate: [],
      pptDate: '',
      pptTime: '',
      registRecruitForm: {
        career: '',
        companyId: localStorage.getItem('id'),
        descriptionURL: '',
        job: '',
        jobDetail: '',
        presentationDate: '',
        recruitStartDate: '',
        recruitEndDate: '',
        recruitURL: '',
      },
      startDateCalendar: false,
      dateCalendar: false,
      descriptionCalendar: false,
    };
  },
  watch: {
    recruitDate() {
      if (this.recruitDate.length >= 1) {
        this.registRecruitForm.recruitStartDate = this.recruitDate[0];
      }
      if (this.recruitDate.length >= 2) {
        this.registRecruitForm.recruitEndDate = this.recruitDate[1];
      }
    },
    pptTime() {
      if (this.pptTime) {
        this.registRecruitForm.presentationDate = this.pptDate + ' ' + this.pptTime;
      }
    },
  },
  methods: {
    registRecruit() {
      if (!this.valid) {
        if (!this.registRecruitForm.job) {
          Swal.fire({ 
            icon: 'warning', // Alert  
            title: ' .', // Alert  
            text: '     .', // Alert  
          });
        } else if (!this.registRecruitForm.career) {
          Swal.fire({ 
            icon: 'warning', // Alert  
            title: '/ .', // Alert  
            text: '     .', // Alert  
          });
        } else if (!this.registRecruitForm.descriptionURL) {
          Swal.fire({ 
            icon: 'warning', // Alert  
            title: ' url .', // Alert  
            text: '     .', // Alert  
          });
        } else if (!this.registRecruitForm.recruitURL) {
          Swal.fire({ 
            icon: 'warning', // Alert  
            title: ' url .', // Alert  
            text: '     .', // Alert  
          });
        }
      } else {
        Http.post('/recruit/upload', this.registRecruitForm)
          .then(() => {
            Swal.fire({ 
              icon: 'success', // Alert  
              title: '  .', // Alert  
              text: ' ! :)', // Alert  
            });
            this.$router.push({ name: 'wanted' });
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    dateCalendarClose() {
      if (this.recruitDate.length === 2) {
        this.dateCalendar = false;
      }
    },
    refreshAll() {
      this.$router.go()
    },
    cancel () {
      Swal.fire({ 
          icon: 'question', // Alert  
          title: ' ?', // Alert 
          text: '   .', 
          showCancelButton: true,
          showConfirmButton: true,
          confirmButtonColor: '#439474',
          confirmButtonText: ``,
          cancelButtonText: ``,
      })
      .then((res) => {
          if(res.isConfirmed) {
              router.push({ name: 'wanted' });
          }
      })
    },
  },
};
</script>

<style></style>
