<template>
<div class="p-b-80">
  <v-form v-model="valid">
    <v-container style="padding-right: 310px; padding-top: 50px">
      <v-row justify="center">
        <v-col cols="4" align="right"><div id="thumb"></div> </v-col>
        <v-col cols="8">
          <v-row>
            <v-img :src="url" max-height="150" max-width="250" />
            <v-col cols="6">
              <v-file-input
                @click:clear="url = this.$store.getters.fileURL + 'ServerFiles/Thumbnail/' + this.lookatme.thumbnail"
                @change="previewImage"
                v-model="image"
                label=" "
                filled
                prepend-icon="mdi-camera"
              >
              </v-file-input>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="4" align="right"></v-col>
        <v-col>
          <v-select
            v-model="category"
            :items="kind"
            item-text="codeName"
            item-value="code"
            label="Select"
            filled
            return-object
            single-line
          ></v-select>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="4" align="right"></v-col>
        <v-col>
          <v-text-field
            v-model="title"
            required
            :rules="[(v) => !!v || ' ']"
            filled
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col cols="4" align="right"></v-col>
        <v-col>
          <v-textarea
            v-model="content"
            required
            :rules="[(v) => !!v || ' ']"
            filled
            height="200"
          ></v-textarea>
        </v-col>
      </v-row>
    </v-container>
    <v-container>
      <v-row justify="center">
        <v-col cols="3">
          <v-btn block text x-large class="primary-color text-white rounded-0" @click="modify">
            
          </v-btn>
        </v-col>
        <v-col cols="3">
          <v-btn block text x-large class="secondary-color text-white rounded-0" @click="cancle">
            
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</div>
</template>

<script>
import router from '@/router';
import http from '@/http.js';

export default {
    props: {
        lookatme: {
            type: [],
            default: null,
        }
    },
    mounted() {
    },
  data() {
    return {
      url: this.$store.getters.fileURL + "ServerFiles/Thumbnail/" + this.lookatme.thumbnail,
      image: null,
      valid: false,
      category: {
        codeName: this.lookatme.category,
      },
      title: this.lookatme.title,
      content: this.lookatme.content,
      kind: [
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '/',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '//',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
        {
          codeName: '',
        },
      ],
    };
  },
  methods: {
    previewImage() {
      this.url = URL.createObjectURL(this.image);
    },
    modify() {
      var router = this.$router;
      if (!this.valid) {
         Swal.fire({ 
          icon: 'warning', // Alert  
          title: '  .', // Alert  
          text: '     .', // Alert  
        });
      } else {
        
        let lookatme = new FormData();
        let userEmail = localStorage.getItem('user_email');
        lookatme.append('lookatmeId', this.lookatme.lookatmeId);
        if(this.image != null){
          lookatme.append('thumbnail', this.image);
        }
        lookatme.append('category', this.category.codeName);
        lookatme.append('title', this.title);
        lookatme.append('content', this.content);
        lookatme.append('userEmail', userEmail);
        
        http.post('/lookatme/update', lookatme, {
            headers: { 'Content-Type': 'multipart/form-data' },
        })
          .then(({ data }) => {
            Swal.fire({ 
              icon: 'success', // Alert  
                title: '  .', // Alert  
                text: '   .', // Alert  
              });
            this.$router.push({name: 'lookatmedetail', query: {lookatme_id: this.lookatme.lookatmeId, thumbnail: this.lookatme.thumbnail, video: this.lookatme.video}});
            
          })
          .catch((err) => console.log(err));
      }
    },

    cancle() {
      router.go(-1);
    },
  },
};
</script>

<style>
#thumb {
  vertical-align: middle;
}
</style>
