<template>
  <div class="profile-menu-box d-flex flex-column justify-content-center m-b-20">
    <div class="d-flex justify-content-center pt-10">
        <v-btn class="blue-button"
            color="white"
            text
            @click="$emit('clickEditButton')"
        >
             
        </v-btn>
        <v-btn class="red-button m-l-10"
            color="white"
            text
            @click="clickDeleteButton"
        >
             
        </v-btn>
    </div>
  </div>
</template>

<script>
export default {
    methods: {
        clickDeleteButton() {
            Swal.fire({ 
                icon: 'question', // Alert  
                title: ' ?', // Alert  
                text: '  .',
                showCancelButton: true,
                showConfirmButton: false,
                showDenyButton: true,
                denyButtonText: ``,
                cancelButtonText: ``,
            })
            .then((res) => {
                if(res.isDenied) {
                    return this.deleteUser()
                }
            })
        },
        deleteUser() {
            this.$store.dispatch('requestDeleteUser', this.$route.params.id)
        }
    }
}
</script>

<style>
.profile-menu-box {
    width: 300px;
    height: 100px;  
}

.blue-button {
    background: #469dff;
    width: 120px;
}

.red-button {
    background: #ff5555;
    width: 110px;
}
</style>