<template>
  <div>
    <v-card class="container mb-5">
      <div class="d-flex justify-content-center">
        <v-card-title>
          <span class="text-h4"> </span>
        </v-card-title>
      </div>
      <v-row class="d-flex justify-content-center">
        <v-col cols="3" class="company-logo-form">
          <div class="company-logo-box">
            <img class="company-logo" :src="imgSrc" alt="logo.png" />
          </div>
          <div class="filebox company-logo-button">
            <label for="company_logo"> </label>
            <input
              type="file"
              id="company_logo"
              @change="changeImage"
              ref="img"
              accept="img/*"
              multiple
            />
          </div>
        </v-col>
        <v-col cols="6">
          <v-form v-model="valid">
            <v-container>
              <v-row>
                <v-col class="py-0" cols="6" md="6">
                  <v-text-field
                    v-model="companySignupForm.companyName"
                    :rules="nameRules"
                    label="*"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="py-0" cols="6" md="6">
                  <v-text-field
                    v-model="companySignupForm.companyEngName"
                    :rules="nameRules"
                    label=" *"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="py-0" cols="6" md="6">
                  <v-text-field
                    v-model="companySignupForm.companyDepartment"
                    :rules="nameRules"
                    label="*"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="py-0" cols="6" md="6">
                  <v-text-field
                    v-model="companySignupForm.companyEngDepartment"
                    :rules="nameRules"
                    label=" *"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="py-0" cols="12" md="10">
                  <v-text-field
                    v-model="companySignupForm.companyId"
                    :rules="idRules"
                    label="*"
                    @input="resetDoubleCheck()"
                    required
                  >
                  </v-text-field>
                </v-col>
                <v-col class="py-0" cols="12" md="2">
                  <v-btn
                    text
                    class="font-weight-bold"
                    style="color: #439474;"
                    @click="doubleCheck()"
                    :disabled="idDoubleCheckDisable"
                  >
                    
                  </v-btn>
                </v-col>
                <v-col class="py-0" cols="12">
                  <v-text-field
                    v-model="companySignupForm.companyPassword"
                    :rules="passwordRules"
                    label="(, ,  )*"
                    type="password"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="py-0" cols="12">
                  <v-text-field
                    v-model="companySignupForm.passwordConfirm"
                    :rules="passwordConfirmRules"
                    label=" *"
                    type="password"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="py-0" cols="12" md="12">
                  <v-text-field
                    v-model="companySignupForm.companyEmail"
                    :rules="emailRules"
                    label="(example@example.com)*"
                    required
                  >
                  </v-text-field>
                </v-col>
                <v-col class="py-0" cols="12">
                  <v-text-field
                    v-model="companySignupForm.companyPhone"
                    :rules="phoneRules"
                    label="(01012345678)*"
                    required
                  ></v-text-field>
                </v-col>
                <v-col class="py-1" cols="12">
                  *  
                </v-col>
              </v-row>
            </v-container>
          </v-form>
        </v-col>
      </v-row>
      <v-container>
        <v-row class="d-flex justify-content-center mt-5">
          <v-col cols="4">
            <v-btn
              block
              text
              x-large
              class="primary-color text-white rounded-0"
              @click="companySignup"
            >
              
            </v-btn>
          </v-col>
          <v-col cols="4">
            <v-btn
              block
              text
              x-large
              class="secondary-color text-white rounded-0"
              @click="closeCompanySignUp"
            >
              
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </div>
</template>

<script>
import Http from '@/http.js';
export default {
  data() {
    return {
      imgSrc: 'images/mypage/noimage.png',
      valid: false,
      nameRules: [
        (v) => !!v || '  ',
        (v) => v.length >= 2 || ' 2 ',
        (v) => v.length <= 30 || ' 30  ',
      ],
      idRules: [
        (v) => !!v || '  ',
        (v) => v.length >= 2 || ' 2 ',
        (v) => v.length <= 30 || ' 30  ',
      ],
      passwordRules: [
        (v) => !!v || '  ',
        (v) => v.length >= 9 || ' 9  ',
        (v) => v.length <= 16 || ' 16  ',
        (v) =>
          (/[0-9]/.test(v) && /[a-zA-Z]/.test(v) && /[~!@#$%^&*()_+|<>?:{}]/.test(v)) ||
          ' , ,   ',
      ],
      passwordConfirmRules: [
        (v) => !!v || '  ',
        (v) =>
          v === this.companySignupForm.companyPassword || '   ',
      ],
      emailRules: [
        (v) => !!v || '  ',
        (v) => (v.includes('@') && v.includes('.')) || '  ',
      ],
      phoneRules: [
        (v) => !!v || '  ',
        (v) =>
          (!v.includes('.') && !v.includes('-')) || '  ',
        (v) =>
          (/[0-9]/.test(v) && !/[a-zA-Z]/.test(v) && !/[~!@#$%^&*()_+|<>?:{}]/.test(v)) ||
          '  ',
      ],
      companySignupForm: {
        companyName: '',
        companyEngName: '',
        companyId: '',
        companyPassword: '',
        passwordConfirm: '',
        companyEmail: '',
        companyPhone: '',
        companyImg: File,
        companyDepartment: '',
        companyEngDepartment: '',
      },
      isIdDoubleChecked: false,
    };
  },
  computed: {
    idDoubleCheckDisable() {
      if (this.isIdDoubleChecked) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    // 
    companySignup() {
      if (!this.valid) {
         Swal.fire({ 
          icon: 'warning', // Alert  
          title: '  .', // Alert  
          text: '     .', // Alert  
        });
      } else if (!this.isIdDoubleChecked) {
        Swal.fire({ 
          icon: 'warning', // Alert  
          title: '   .', // Alert  
          text: '     .', // Alert  
        });
      } else {
        const formData = new FormData();

        formData.append('companyId', this.companySignupForm.companyId);
        formData.append('companyName', this.companySignupForm.companyName);
        formData.append('companyNameEng', this.companySignupForm.companyEngName)
        formData.append('companyEmail', this.companySignupForm.companyEmail);
        formData.append('companyPassword', this.companySignupForm.companyPassword);
        formData.append('companyPhone', this.companySignupForm.companyPhone);
        formData.append('companyImg', this.companySignupForm.companyImg);
        formData.append('companyDept', this.companySignupForm.companyDepartment);
        formData.append('companyDeptEng', this.companySignupForm.companyEngDepartment)

        Http.post('company/signup', formData, {
          headers: { 'Content-Type': 'multipart/form-data' },
        })
          .then((res) => {
            console.log(res);
            Swal.fire({ 
              icon: 'success', // Alert  
              title: '  .', // Alert  
              text: '!!  :)', // Alert  
            });
            this.companySignupForm.companyName = '';
            this.companySignupForm.companyEngName = '';
            this.companySignupForm.companyId = '';
            this.companySignupForm.companyPassword = '';
            this.companySignupForm.passwordConfirm = '';
            this.companySignupForm.companyEmail = '';
            this.companySignupForm.companyPhone = '';
            this.companySignupForm.companyDepartment = '';
            this.companySignupForm.companyEngDepartment = '';
            this.$router.push('/mypage/admin/');
          })
          .catch((err) => {
            Swal.fire({ 
              icon: 'error', // Alert  
              title: '  .', // Alert  
              text: '  ', // Alert  
            });
            console.log(err);
          });
      }
    },
    //  
    doubleCheck() {
      if (this.companySignupForm.companyId) {
        const url = '/company/companyidcheck/' + this.companySignupForm.companyId;
        Http.get(url)
          .then((res) => {
            if (res.status === 201) {
              this.isIdDoubleChecked = true;
              Swal.fire({ 
                icon: 'success', // Alert  
                title: '   .', // Alert  
                text: '  .', // Alert  
              });
            } else if (res.status === 202) {
              this.isIdDoubleChecked = false;
              Swal.fire({ 
                icon: 'error', // Alert  
                title: ' .', // Alert  
                text: '    .', // Alert  
              });
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.isIdDoubleChecked = false;
          Swal.fire({ 
            icon: 'warning', // Alert  
            title: ' .', // Alert  
            text: '     .', // Alert  
          });
      }
    },
    //   false 
    resetDoubleCheck() {
      this.isIdDoubleChecked = false;
    },
    closeCompanySignUp() {
      this.companySignupForm.companyName = '';
      this.companySignupForm.companyId = '';
      this.companySignupForm.companyPassword = '';
      this.companySignupForm.passwordConfirm = '';
      this.companySignupForm.companyEmail = '';
      this.companySignupForm.companyPhone = '';
      this.companySignupForm.companyDepartment = '';
      this.$router.push('/mypage/admin/');
    },
    changeImage(e) {
      const file = e.target.files[0];
      this.companySignupForm.companyImg = file;
      this.imgSrc = URL.createObjectURL(file);
    },
  },
};
</script>

<style>
.primary-color {
  background: #439474;
}
.secondary-color {
  background: #7d7d7d;
}
.go-to-login:hover {
  text-decoration: underline;
}

.company-signup-image-form {
  border: black 1px solid;
}

.company-logo-box {
  position: relative;
  height: 200px;
  width: 200px;
  border-radius: 70%;
  overflow: hidden;
  border: #439474 1px solid;
}

.company-logo {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.company-logo-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.company-logo-button {
  position: relative;
  left: 50px;
  top: 50px;
  width: 100px;
}
</style>
