<template>
  <v-form v-model="valid">
    <v-container class="m-t-50" style="max-width: 800px; background: #EAF5F1;">
      <v-row class="justify-center">
        <v-col cols="12">
          <v-row class="justify-center">
            <v-col cols="4">
              <v-img :src="url" max-height="150" max-width="250" />
            </v-col>
            <v-col cols="6">
              <v-file-input
                @click:clear="url = 'images/lookatme/img.jpg'"
                @change="previewImage"
                v-model="image"
                label=" "
                prepend-icon="mdi-camera"
              >
              </v-file-input>
              <v-file-input
                v-model="files"
                required
                :rules="[(v) => !!v || ' ']"
                label=" "
                show-size
              ></v-file-input>
            </v-col>
          </v-row>
        </v-col>
        <v-col>
          <v-row>
            <v-col cols="2">
              <v-select
                v-model="category"
                :items="kind"
                item-text="codeName"
                item-value="code"
                label=""
                return-object
                single-line
              ></v-select>
            </v-col>
            <v-col cols="10">
              <v-text-field
                v-model="title"
                required
                label=""
                :rules="[(v) => !!v || ' ']"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12">
          <v-row>
            <v-col>
              <v-textarea
                v-model="content"
                required
                label=""
                :rules="[(v) => !!v || ' ']"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
    <v-container style="max-width: 800px; height: 200px;">
      <v-col>
        <v-row class="d-flex justify-content-end">
          <v-col cols="3">
            <v-btn block text x-large class="primary-color text-white rounded-0" @click="regist">
              
            </v-btn>
          </v-col>
          <v-col cols="3">
            <v-btn block text x-large class="secondary-color text-white rounded-0" @click="cancel">
              
            </v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-container>
  </v-form>
</template>

<script>
import router from '@/router';
import http from '@/http.js';

export default {
  data() {
    return {
      url: 'images/lookatme/img.jpg',
      image: null,
      files: [],
      valid: false,
      category: {
        codeName: '',
        code: '1001',
      },
      title: '',
      content: '',
      kind: [
        {
          codeName: '',
          code: '1001',
        },
        {
          codeName: '',
          code: '1002',
        },
        {
          codeName: '',
          code: '1003',
        },
        {
          codeName: '',
          code: '2001',
        },
        {
          codeName: '/',
          code: '2002',
        },
        {
          codeName: '',
          code: '3001',
        },
        {
          codeName: '',
          code: '3002',
        },
        {
          codeName: '',
          code: '3003',
        },
        {
          codeName: '',
          code: '3004',
        },
        {
          codeName: '',
          code: '3005',
        },
        {
          codeName: '',
          code: '3006',
        },
        {
          codeName: '',
          code: '3007',
        },
        {
          codeName: '//',
          code: '3008',
        },
        {
          codeName: '',
          code: '3009',
        },
        {
          codeName: '',
          code: '3010',
        },
        {
          codeName: '',
          code: '3011',
        },
        {
          codeName: '',
          code: '3012',
        },
        {
          codeName: '',
          code: '4001',
        },
        {
          codeName: '',
          code: '4002',
        },
        {
          codeName: '',
          code: '4003',
        },
      ],
    };
  },
  methods: {
    previewImage() {
      this.url = URL.createObjectURL(this.image);
    },
    regist() {
      var router = this.$router;
      if (!this.valid) {
        Swal.fire({ 
          icon: 'warning', // Alert  
          title: '  .', // Alert  
          text: '     .', // Alert  
          confirmButtonColor: '#439474',
        });
      } else {
        let lookatme = new FormData();
        let userEmail = localStorage.getItem('user_email');
        lookatme.append('thumbnail', this.image);
        lookatme.append('video', this.files);
        lookatme.append('category', this.category.codeName);
        lookatme.append('title', this.title);
        lookatme.append('content', this.content);
        lookatme.append('userEmail', userEmail);
        http.post('/lookatme/upload', lookatme, {
          headers: { 'Content-Type': 'multipart/form-data' },
        })
          .then(({ data }) => {
            Swal.fire({ 
              icon: 'success', // Alert  
              title: '  .', // Alert  
              text: '   .', // Alert  
              confirmButtonColor: '#439474',
            });
            router.push({ name: 'lookatme' });
          })
          .catch((err) => console.log(err));
      }
    },
    cancel() {
      Swal.fire({ 
          icon: 'question', // Alert  
          title: ' ?', // Alert 
          text: '   .', 
          showCancelButton: true,
          showConfirmButton: true,
          confirmButtonColor: '#439474',
          confirmButtonText: ``,
          cancelButtonText: ``,
      })
      .then((res) => {
          if(res.isConfirmed) {
              router.push({ name: 'lookatme' });
          }
      })
    },
  },
};
</script>

<style>
#thumb {
  vertical-align: middle;
}
</style>
