<template>
<v-card class="px-5">
  <div class="d-flex justify-content-center py-3">
    <v-card-title>
      <span class="text-h4"></span>
    </v-card-title>
  </div>
  <v-form v-model="valid">
    <v-container>
      <v-row>
        <v-col class="py-0" cols="12" md="6">
          <v-text-field
            v-model="loginForm.userName"
            :rules="nameRules"
            label="*"
            required
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="6">
          <v-text-field
            v-model="loginForm.birth"
            :rules="birthRules"
            label="*"
            type="date"
            required
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="10">
          <v-text-field
            v-model="loginForm.userNickname"
            :rules="nickNameRules"
            label="*"
            @input="resetDoubleCheck('nickName')"
            required
          ></v-text-field>
        </v-col>
        <v-col class="py-0 d-flex flex-column justify-content-center" cols="12" md="2">
          <v-btn
            text
            class="d-flex align-text-bottom font-weight-bold"
            style="color: #439474;"
            @click="doubleCheck('nickName')"
            :disabled="nickNameDoubleCheckDisable">
            
          </v-btn>
        </v-col>
        <v-col class="py-0" cols="12" md="10">
          <v-text-field
            v-model="loginForm.userEmail"
            :rules="emailRules"
            label="(example@example.com)*"
            @input="resetDoubleCheck('email')"
            required>
          </v-text-field>
        </v-col>
        <v-col class="py-0 d-flex flex-column justify-content-center" cols="12" md="2">
          <v-btn
            text
            class="font-weight-bold"
            style="color: #439474;"
            @click="doubleCheck('email')"
            :disabled="emailDoubleCheckDisable">
            
          </v-btn>
        </v-col>
        <v-col class="py-0" cols="12">
          <v-text-field
            v-model="loginForm.userPhone"
            :rules="phoneRules"
            label="(01012345678)*"
            required
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12">
          <v-text-field
            v-model="loginForm.userPassword"
            :rules="passwordRules"
            label="(, ,  )*"
            type="password"
            required
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12">
          <v-text-field
            v-model="loginForm.passwordConfirm"
            :rules="passwordConfirmRules"
            label=" *"
            type="password"
            required
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="6">
          <v-text-field
            v-model="loginForm.major1"
            :rules="majorRules"
            label="1"
          ></v-text-field>
        </v-col>
        <v-col class="py-0" cols="12" md="6">
          <v-text-field
            v-model="loginForm.major2"
            :rules="majorRules"
            label="2"
          ></v-text-field>
        </v-col>
        <v-col class="py-1" cols="12">
          <large>*  </large>
        </v-col>
      </v-row>
    </v-container>
    <v-container>
      <v-row>
        <v-col cols="6">
          <v-btn block text x-large class="primary-color text-white rounded-0" @click="signup">
            
          </v-btn>
        </v-col>
        <v-col cols="6">
          <v-btn block text x-large class="secondary-color text-white rounded-0" @click="closeSignUpModal">
            
          </v-btn>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="6">
          <p style="cursor: pointer;" class="subtitle-1 text-center go-to-login" @click="$emit('login')">
             ?
          </p>
        </v-col>
        <v-col cols="6">
          <p class="subtitle-1 text-center" style="line-height: 120%;" @click="$emit('login')">
               
            profileglance@profileglance.com
          </p>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</v-card>
</template>

<script>
import Http from '@/http.js'
export default {
  data () {
    return {
      valid: false,
      nameRules: [
        v => !!v || '  ',
        v => v.length >= 2 || ' 2 ',
        v => v.length <= 30 || ' 30  ',
      ],
      birthRules: [
        v => !!v || '  ',
      ],
      nickNameRules: [
        v => !!v || '  ',
        v => v.length >= 2 || ' 2  ',
        v => v.length <= 30 || ' 30  ',
      ],
      emailRules: [
        v => !!v || '  ',
        v => (v.includes('@') && v.includes('.')) || '  ',

      ],
      phoneRules: [
        v => !!v || '  ',
        v => !v.includes('.') && !v.includes('-') || '  ',
        v => /[0-9]/.test(v) && !/[a-zA-Z]/.test(v) && !/[~!@#$%^&*()_+|<>?:{}]/.test(v) || '  '
      ],
      passwordRules: [
        v => !!v || '  ',
        v => v.length >= 9 || ' 9  ',
        v => v.length <= 16 || ' 16  ',
        v => /[0-9]/.test(v) && /[a-zA-Z]/.test(v) && /[~!@#$%^&*()_+|<>?:{}]/.test(v) || ' , ,   '
      ],
      passwordConfirmRules: [
        v => !!v || '  ',
        v => v === this.loginForm.userPassword || '   ',
      ],
      majorRules: [
        v => v.length <= 16 || ' 16  '
      ],
      loginForm: {
        userName: '',
        userNickname: '',
        birth: '',
        userEmail: '',
        userPhone: '',
        userPassword: '',
        passwordConfirm: '',
        major1: '',
        major2: '',
      },
      isNickNameDoubleChecked: false,
      isEmailDoubleChecked: false,
    }
  },
  computed: {
    nickNameDoubleCheckDisable () {
      if (this.isNickNameDoubleChecked) {
        return true
      } else {
        return false
      }
    },
    emailDoubleCheckDisable () {
      if (this.isEmailDoubleChecked) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    // 
    signup () {
      if (!this.valid) {
        Swal.fire({ 
          icon: 'warning', // Alert  
          title: '  .', // Alert  
          text: '     .', // Alert  
        });
      }
      else if (!this.isNickNameDoubleChecked) {
        Swal.fire({ 
          icon: 'warning', // Alert  
          title: '   .', // Alert  
          text: '     .', // Alert  
        });
      }
      else if (!this.isEmailDoubleChecked) {
        Swal.fire({ 
          icon: 'warning', // Alert  
          title: '   .', // Alert  
          text: '     .', // Alert  
        });
      }
      else {
        Http.post('/user/signup', this.loginForm)
        .then(() => {
          Swal.fire({ 
            icon: 'success', // Alert  
            title: ' .', // Alert  
            text: '!!  :)', // Alert  
          });
          this.loginForm.userName = ''
          this.loginForm.userNickname = ''
          this.loginForm.birth = ''
          this.loginForm.userEmail = ''
          this.loginForm.userPhone = ''
          this.loginForm.userPassword = ''
          this.loginForm.passwordConfirm = ''
          this.loginForm.major1 = ''
          this.loginForm.major2 = ''
          this.$emit('close')
        })
        .catch((err) => {
          Swal.fire({ 
            icon: 'error', // Alert  
            title: ' .', // Alert  
            text: '  ', // Alert  
          });
        })
      }
    },
    // & 
    doubleCheck (target) {
      if (target === 'nickName') {
        if (this.loginForm.userNickname) {
          const url = '/user/usernicknamecheck/' + this.loginForm.userNickname
          Http.get(url)
          .then((res) => {
            if (res.status === 201) {
              this.isNickNameDoubleChecked = true
              Swal.fire({ 
                icon: 'success', // Alert  
                title: '   .', // Alert  
                text: '  .', // Alert  
              });
            } else if (res.status === 202) {
              this.isNickNameDoubleChecked = false
              Swal.fire({ 
                icon: 'error', // Alert  
                title: ' .', // Alert  
                text: '    .', // Alert  
              });
            }
          })
          .catch((err) => {
          })
        } else {
          this.isNickNameDoubleChecked = false
          Swal.fire({ 
            icon: 'warning', // Alert  
            title: ' .', // Alert  
            text: '     .', // Alert  
          });
        }
      } else if (target === 'email') {
        if (this.loginForm.userEmail) {
          const url = '/user/useremailcheck/' + this.loginForm.userEmail
          Http.get(url)
          .then((res) => {
            if (res.status === 201) {
              this.isEmailDoubleChecked = true
              Swal.fire({ 
                icon: 'success', // Alert  
                title: '   .', // Alert  
                text: '  .', // Alert  
              });
            } else if (res.status === 202) {
              this.isEmailDoubleChecked = false
              Swal.fire({ 
                icon: 'error', // Alert  
                title: ' .', // Alert  
                text: '    .', // Alert  
              });
            }
          })
          .catch((err) => {
          })
        } else {
          this.isEmailDoubleChecked = false
          Swal.fire({ 
            icon: 'warning', // Alert  
            title: ' .', // Alert  
            text: '     .', // Alert  
          });
        }
      }
    },
    //   false 
    resetDoubleCheck (target) {
      if (target === 'nickName') {
        this.isNickNameDoubleChecked = false
      } else if (target === 'email') {
        this.isEmailDoubleChecked = false
      }
    },
    //   
    closeSignUpModal () {
      this.loginForm.userName = ''
      this.loginForm.userNickname = ''
      this.loginForm.birth = ''
      this.loginForm.userEmail = ''
      this.loginForm.userPhone = ''
      this.loginForm.userPassword = ''
      this.loginForm.passwordConfirm = ''
      this.loginForm.major1 = ''
      this.loginForm.major2 = ''
      this.$emit('close')
    }
  }
}
</script>

<style>
.primary-color {
  background: #439474;
}
.secondary-color {
  background: #7D7D7D;
}
.go-to-login:hover {
  text-decoration: underline;
}
</style>
